html {
    height: 100%;
    max-width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}

body {
    box-sizing: border-box;
    margin: 0px;
    background-color: white;
  }

  a {
      text-decoration: none;
      color: #1fb26f;
  }

  h1, p {
      padding: 15px;
  }

  h1 {
      font-size: 1.3em;
      font-family: 'Open Sans Condensed', sans-serif;
      margin: 0;
  }

  h2 {
    font-family: 'Open Sans Condensed', sans-serif;
    color: white;
    padding-left: 20px;
  }

  p {
    font-family: 'Heebo', sans-serif;
    font-size: 14px;
    // letter-spacing: 1px;   
    font-weight: 400; 
    color: #333333;
    text-align: justify;
    line-height: 20px;
    margin: 0;
  }


  @media (min-width: 900px) {
    h1 {
        font-size: 1.8em;
    }

    p {
        font-size: 16px;
        line-height: 24px;
    }
  }

  .white-text-on-pic {
      color: white;
      text-shadow: 1px 1px 4px rgba(0,0,0,.8);
  }


  .container > div.flex-div {
    display: flex;
    justify-content: center;
    align-content: center;
  }
  
  .container {
    display: grid;
    // grid-gap: 3px;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows:  
    auto //logo
    auto //nav
    auto //social
    40vh //marquee-1
    auto //intro
    minmax(230px, 30vh) //services marquee-2    
    auto //service 1
    auto //service 2
    auto //service 3
    minmax(230px, 30vh) //clients marquee-3
    auto //client logos
    100vh //sample 1
    100vh //sample 2
    100vh //sample 2
    auto // footer left
    auto //footer mid
    auto; //footer right
}

@media(min-width: 900px) {
    .container {

        grid-template-rows: auto auto auto 40vh auto  minmax(230px, 60vh) auto  minmax(230px, 60vh) auto 100vh 100vh 100vh 100vh ;
    }
}

.logo {
    grid-column: 1 / -1;
    background-color: white;
}

.logo-image {
    width: 150px;
    padding: 10px 0px;
    object-fit: contain;
}

.menu {
    grid-column: 1 / -1;
    grid-row: 2 / span 1;
    background-color: white;
    letter-spacing: 2px;
}

.menu > div {
    max-width: 80px;
    margin: 1em;
}

.menu a {
    color: #333333;
}

.social-icons-bar {
    grid-column: 1 / -1;
    grid-row: 3 / span 1;
    background-color: #1fb26f;
}

.social-icons-bar > img {
    margin: .25em 1em;
}

.marquee-1 {
    grid-column: 1 / -1;
    grid-row: 4 / span 1;
    background-image: url("/Users/sarahwaldie/Desktop/SBS-website-2018/Images/marble-bg.jpg");
    background-size: cover;
    background-attachment: fixed;
}
    

.content-1 {
    grid-column: 1 / span 5;
    grid-row: 5 / span 1;
    align-items: top !important;
    background-color: white;
    text-align: right;
    padding: 20px 0 0 0;

}

.content-2 {
    grid-column: 6 / -1;
    grid-row: 5 / span 1;
    background-color: white;
    text-align: justify;
    padding: 20px;
}

@media (min-width: 900px) {
    .content-1 {
    grid-column: 3 / span 3;
    }

    .content-2 {
    grid-column: 6 / -3;  
    }
}

.marquee-2 {
    grid-column: 1 / -1;
    grid-row: 6 / span 1;
    background-image: url("/Users/sarahwaldie/Desktop/SBS-website-2018/Images/Asset 1.jpg");
    background-size: cover;
    background-attachment: fixed;
    align-items: center;
}

.animation-element1 {
    height: 200px;
    width: 200px;
    border-radius: 100px;
    background-color: #1fb26f;
    opacity: 0;
    position: relative;
    display: flex;
    justify-content: center;
    align-content: center;
}

.animation-element1 > h2 {
    padding-top: 90px;
    padding-left: 30px;
}

.animation-element2 {
    height: 200px;
    width: 200px;
    border-radius: 100px;
    background-color: #1fb26f;
    opacity: 0;
    position: relative;
    display: flex;
    justify-content: center;
    align-content: center;
}

.animation-element2 > h2 {
    padding-top: 90px;
    padding-left: 30px;
}


  @keyframes Slide-Left {
    0%   { left: -90%; }
    100% { left: 0%; }
  }

.in-view {
    opacity: 1;
    animation: Slide-Left 3s;
}

.service-1 {
    grid-column: 1 / -1;
    grid-row: 7 / span 1;
    background-color: white;
    position: relative;
    z-index: 200;
    padding: 20px;
    border: 1px solid #888888;
    margin: 1em;
    border-radius: 10px;
}


.service-2 {
    grid-column: 1 / -1;
    grid-row: 8 / span 1;
    background-color: white;
    position: relative;
    z-index: 200;
    padding: 20px;
    border: 1px solid #888888;
    margin: 1em;
    border-radius: 10px;
}

.service-3 {
    grid-column: 1 / -1;
    grid-row: 9 / span 1;
    background-color: white;
    position: relative;
    z-index: 200;
    padding: 20px;
    border: 1px solid #888888;
    margin: 1em;
    border-radius: 10px;
}

@media(min-width: 900px) {
    .service-1 {
    grid-column: 1 / span 4;
    grid-row: 7 / span 1;
    }

    .service-2 {
        grid-column: 5 / span 4;
        grid-row: 7 / span 1;
        }

    .service-3 {
    grid-column: 9 / span 4;
    grid-row: 7 / span 1;
    }
}

.marquee-3 {
    grid-column: 1 / -1;
    grid-row: 10 / span 1;
    background-image: url("/Users/sarahwaldie/Desktop/SBS-website-2018/Images/typewriter.jpeg");
    background-size: cover;
    background-attachment: fixed;
    align-items: center;
}

@media(min-width: 900px) {
    .marquee-3 {
        grid-row: 8 / span 1;
    }
}



.clients {
    
    grid-column: 1 / -1;
    grid-row: 11 / span 1;
    background-color: black;
    padding: 20px 0;
    position: relative;
    z-index: 200;
}

@media(min-width: 900px) {
    .clients {
        grid-row: 9 / span 1;
    }
}

.clients > p {
    display: block;
    width: 100%;
    text-align: center;
    margin: 0;
    color: white;
    font-weight: 100;
    letter-spacing: 2px;
    
}

.to-center-logos {
    display: block;
    margin: 40px auto 0 auto;
    max-width: 1080px;
}

.client-logos {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.client-logos > div > a > img {
    width: 120px;
    padding: 15px;
    transition: filter .5s ease-in-out;
    -webkit-filter: grayscale(100%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
    filter: grayscale(100%); /* FF 35+ */
}

.client-logos > div > a > img:hover {
    -webkit-filter: grayscale(0%); /* Ch 23+, Saf 6.0+, BB 10.0+ */
    filter: grayscale(0%); /* FF 35+ */
  }

@media(min-width: 900px) {
    .client-logos {
        margin: 60px 0;
    }

    .client-logos > div > a > img {
        width: 150px;
        padding: 25px;

    }
}

.work-sample {
    padding-bottom: 40px;
    padding-top: 40px;
    display: flex;
    align-items: center;

}

.work-sample-internal {
    width: 80%;
    display: block;
    margin: 0 auto;
    max-width: 1080px;
    height: auto;
    background-color: black;
    padding: 20px;
}

.work-sample-internal > h3 {
    color: #888888;
    border-bottom: 1px solid #888888;
}

.work-sample-internal > p {
    color: #888888;
    padding-left: 0px;
    margin-left: 0px;
}

.work-sample-internal > a {
    color: #888888;
}

.web-sample-image {
    display: block;
    object-fit: contain;
    width: 100%;
    max-width: 1080px;
    margin: 0 auto;
        // max-height: 100%;
}

.navigation-arrows {
    width: 100%;
    text-align: center;
}

.navigation-arrows h5{
    padding: 0px;
    margin: 60px 0px 10px 0px;
    color: #888888;
   }

.navigation-arrows a{
 color: #888888;
}

@media (min-width: 900px) {
    .navigation-arrows h5{
    margin: 30px 0px 10px 0px;

       }
}

